import React, { useEffect } from 'react'

const useOnClickOutside = (ref, handler) => {//   ref handler    .
    useEffect(() => {
        const listener = (event) =>{//
            console.log('ref', ref.current) //ref.current    .
            if(!ref.current || ref.current.contains(event.target)) {
                return;
            }
            handler();
        }; 

        document.addEventListener("mousedown", listener) //mousedown  , listener .
        document.addEventListener("touchstart", listener)//touchstart  , listener .
        return () => {
            document.addEventListener("mousedown", listener)//  listener  .
            document.addEventListener("touchstart", listener)//  listener  .
        };
    }, []);
};

export default useOnClickOutside; // useOnClickOutside   .